<template>
  <div class="container py-5">
    <NavTabs />
    <form class="w-100" @submit.prevent.stop="registerSubmit">
      <div class="text-center mb-4">
        <h1 class="h3 mb-3 font-weight-normal">
          註冊 Sign Up
        </h1>
      </div>

      <div class="form-label-group mb-2">
        <label for="name">姓名 Name</label>
        <input
          id="name"
          v-model="name"
          name="name"
          type="text"
          class="form-control"
          placeholder="name"
          autocomplete="username"
          required
          autofocus
        >
      </div>

      <div class="form-label-group mb-2">
        <label for="email">信箱 Email</label>
        <input
          id="email"
          v-model="email"
          name="email"
          type="email"
          class="form-control"
          placeholder="email"
          autocomplete="email"
          required
        >
      </div>

      <div class="form-label-group mb-3">
        <label for="password">密碼 Password</label>
        <input
          id="password"
          v-model="password"
          name="password"
          type="password"
          class="form-control"
          placeholder="Password"
          autocomplete="new-password"
          required
          pattern="^([a-zA-Z]+\d+|\d+[a-zA-Z]+)[a-zA-Z0-9]*$" 
          minlength="8" 
          maxlength="12" 
          title="請最少輸入8-12位英文與數字"
        >
      </div>

      <div class="form-label-group mb-3">
        <label for="password-check">確認密碼 Password Check</label>
        <input
          id="password-check"
          v-model="passwordCheck"
          name="passwordCheck"
          type="password"
          class="form-control"
          placeholder="Password"
          autocomplete="new-password"
          required
          pattern="^([a-zA-Z]+\d+|\d+[a-zA-Z]+)[a-zA-Z0-9]*$" 
          minlength="8" 
          maxlength="12" 
          title="請最少輸入8-12位英文與數字"
        >
      </div>

      <button
        class="btn btn-lg btn-primary btn-block mb-3"
        type="submit"
        :disabled="isProcessing"
      >
        提交 Submit
      </button>

      <div class="text-center mb-3">
        <p>
          已有帳號? 
          <router-link to="/signin">
            登入 Sign In
          </router-link>
        </p>
      </div>
    </form>
  </div>
</template>

<script>
import NavTabs from '../components/NavTabs.vue'
import { Toast } from '../utils/helpers'
import axios from 'axios'
import AuthService from '../services/authorization'

export default {
  components: {
    NavTabs
  },
  data() {
    return {
      name: '',
      password: '',
      passwordCheck: '',
      isProcessing: false
    }
  },
  methods: {
    async registerSubmit() {
      try {
        this.isProcessing = true
        // 確認四個欄位都有填寫
        if (!this.name.trim() || !this.email || !this.password || !this.passwordCheck) {
          Toast.fire({
            icon: 'warning',
            title: '有欄位未輸入'
          })
          this.isProcessing = false
          return 
        } 

        // email是否重複使用
        const responseEmail = await axios({
          method: 'post',
          url: 'users',
          data: {
            email: this.email
          }
        })
        if (responseEmail.data.email) {
          this.isProcessing = false
          Toast.fire({ icon: 'warning', title: '此信箱已註冊!' })
          return 
        }

        // 密碼和確認密碼是否一致
        if (this.password !== this.passwordCheck) {
          this.isProcessing = false
          Toast.fire({ icon: 'warning', title: '密碼與確認密碼不符!' })
          return
        }

        // 註冊
        const response = await AuthService.signUp(this.name, this.email, this.password)

        if (response.data.user) {
          localStorage.removeItem('token')
          localStorage.setItem('token', response.data.token)
          
          this.$store.commit('setCurrentUser', response.data.user)
          this.$router.push('/')

          Toast.fire({ icon: 'success', title: 'Registered successfully!' })
          this.isProcessing = false
          return
        } 
        if (response.data.message) {
          Toast.fire({ icon: 'warning', title: response.data.message })
        }
      } catch (error) {
        console.log(error)
      }
    }
  }
}
</script>